@import url(https://fonts.googleapis.com/css2?family=Comme:wght@600&display=swap);
html{background-color:lightcoral}.App{background-color:lightcoral;max-width:1300px;margin:0 auto;text-align:center;font-family:"Comme", sans-serif}.App__title{color:#fff}

.Carousel{background-color:lightcoral}.Carousel__container{display:flex;justify-content:center;overflow:hidden;margin:30px auto;border:1px solid #fff;border-radius:30px}.Carousel__buttons{display:flex;justify-content:center;grid-gap:30px;gap:30px;margin-bottom:20px}.Carousel__list{display:flex;padding:0;margin:0}.Carousel__list li{list-style:none}.Carousel__control{display:flex;justify-content:center;grid-gap:30px;gap:30px;width:-webkit-max-content;width:max-content;border:1px solid #fff;margin:auto;padding:30px;border-radius:30px}.Carousel__labels{display:flex;flex-direction:column;grid-gap:10px;gap:10px;text-align:left;color:#fff}.Carousel__inputs{display:flex;flex-direction:column;grid-gap:20px;gap:20px}

